import { graphql } from "gatsby";
import React from "react";
import { IPost, IQueryArrayResult, ICategory, IAuthor, IComment, ITag, IGatsbyLocation } from '../../interfaces';
import { ContentLayoutWithSidebar } from "../../components/Layout/ContentLayout";
import { SideBar } from "../../components/SideBar/SideBar";
import { SideBarContent } from "../../components/SideBar/SideBarContent";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import { CategoryBar } from "../../components/CategoryBar/CategoryBar";
import { PostHeader } from "../../components/Post/PostHeader";
import { ReactHTMLContent } from "../../components/Content/Content";
import { getCategory, nestCategories } from '../../tool';
import { ContentFooter } from "../../components/ContentFooter/ContentFooter";
import { PostFooter } from "../../components/ContentFooter/PostFooter";
import { NextPrevPostFooter } from "../../components/ContentFooter/NextPrevPostFooter";
import { CommentFooter } from "../../components/ContentFooter/CommentFooter";
import { SubscriberFooter } from '../../components/ContentFooter/SubscriberFooter';
import { AuthorFooter } from "../../components/ContentFooter/AuthorFooter";
import { IHeaderProps } from "../../components/Layout/Header";

interface IQueryResult
{
  data: {
    post: IPost,
    categories: IQueryArrayResult<ICategory>
    comments: IQueryArrayResult<IComment>
    tags: IQueryArrayResult<ITag>
    avatar: any;
    image: any;
  }
  context: {
    prev: {title: string; route: string;};
    next: {title: string; route: string;};
    author: IAuthor;
    comments: IComment[];
  };
  location: IGatsbyLocation;
}

export class PostPage extends React.Component<IQueryResult>
{
  constructor(props)
  {
    super(props);
  }
  render(){
    let post = this.props.data.post;
    let context = this.props.context;

    let categories = nestCategories(this.props.data.categories.edges.map(e => e.node));
    let cat = post.frontmatter.categories.map(c => getCategory(c, categories));
    let tagNodes = this.props.data.tags.edges.map(n => n.node);
    let tags = this.props.data.post.frontmatter.tags.map(t => tagNodes.find(tag => tag.slug == t));
    let header: IHeaderProps = {
      url: this.props.data.post.frontmatter.slug,
      description: post.frontmatter.description,
      type: "article",
      title: post.frontmatter.title,
      tags: tags.map(t => t.name),
      image: this.props.data.post.frontmatter.image != null ? this.props.data.image.childImageSharp.fluid.src : null,
      updated_time: post.frontmatter.updatedate,
      publish_time: post.frontmatter.date
    }

    return (
      <ContentLayoutWithSidebar 
        sidebar={(
          <SideBar isFixed={true}>
            <SideBarContent tableOfContents={post.tableOfContents} />
            <SideBarHelp/>
          </SideBar>
        )} 
        categoryBar={(<CategoryBar title={cat.map(c => c.friendlyname).join(" - ")}/>)}
        header={header}
      >
        <PostHeader frontmatter={post.frontmatter} commentCnt={post.fields.commentCnt} author={context.author} categories={cat} isPreview={false}/>
        <div className="post-content">
          {/* <HTMLContent content={post.html}></HTMLContent> */}
          <ReactHTMLContent html={post.htmlAst}></ReactHTMLContent>
          {
            (post.fields.subscriberContent == null || post.fields.subscriberContent.length == 0) ?          
            ""
            : 
            <SubscriberFooter content={post.fields.subscriberContent}></SubscriberFooter> 
          }
        </div>
        <ContentFooter>
          <PostFooter tags={tags} categories={cat} />
          <NextPrevPostFooter nextPost={context.next} previousPost={context.prev}></NextPrevPostFooter>
          <AuthorFooter author={this.props.context.author} avatar={this.props.data.avatar}></AuthorFooter>
          <CommentFooter slug={post.frontmatter.slug} comments={context.comments}></CommentFooter>
        </ContentFooter>
      </ContentLayoutWithSidebar>
    );
  }
}

const Page = ({data, pageContext, location}) => {
  return (
    <PostPage location={location} data={data} context={pageContext}></PostPage>
  );
};
export default Page;

export const pageQuery = graphql`
  query BlogPostByID($id: String!, $avatar: String!, $image: String!) {
    post: markdownRemark(
      id: { eq: $id }
      fileAbsolutePath: {
        regex: "/.*\/data\/posts\/.*[.]md/"
      }
    ){
      ...post
    }
    avatar: file(
    	absolutePath: { regex: "/.*\/data\/img\/*/"}  
      name:{ eq: $avatar}
    ){
      childImageSharp {
        fixed(width: 100, height: 100) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    image: file(
    	absolutePath: { eq: $image}  
    ){
      childImageSharp {
        fluid(maxWidth: 2000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    ...categories
    ...tags
}
`;
