import React from "react";
import { FaArrowAltCircleRight, FaArrowAltCircleLeft } from "react-icons/fa";
import "./ContentFooter.sass";
import { Link } from 'gatsby';
interface IProps
{
  nextPost: {title: string; route: string;};
  previousPost: {title: string; route: string;}
}

export class NextPrevPostFooter extends React.Component<IProps>
{
  render()
  {
    return (
      <>
        <div className="next-prev-container">
          <div style={{flexBasis: "content"}}>
          {
            this.props.previousPost == null ? "" : (
              <Link to={"/"+this.props.previousPost.route} title={this.props.previousPost.title}>
                <span style={{verticalAlign: "middle", marginRight: "10px"}}>  
                  <FaArrowAltCircleLeft/>
                </span>
                <span>
                  {
                    this.props.previousPost.title.length <= 28 ? this.props.previousPost.title : this.props.previousPost.title.substr(0, 25) + "..."
                  }
                </span>
              </Link>
            )
          }
          </div>
          <div style={{flexBasis: "content"}}>
          {
            this.props.nextPost == null ? "" : (
              <Link to={"/"+this.props.nextPost.route} title={this.props.nextPost.title}>
                <span>
                  {
                    this.props.nextPost.title.length <= 28 ? this.props.nextPost.title : this.props.nextPost.title.substr(0, 25) + "..."
                  }
                </span>
                <span className="next-prev-icon">
                  <FaArrowAltCircleRight/>
                </span>
              </Link>
            )
          }
          </div>
        </div>
      </>
    )
  }
}